.App {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  .main {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    width: 470px;
    max-width: 470px;
    height: 570px;
    max-height: 570px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    position: relative;
    left: 0;
    z-index: 10;
    transition: left .7s ease-out;
    &.active {
      left: -242px;
      z-index: 10;
    }
    .card {
      height: 100%;
      overflow-y: auto;
      overflow-x: hidden;
      border-top: 6px solid #021533;
      top: -6px;
    }
  }
}

